@import '~antd/dist/antd.css';

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: rgb(243, 243, 243);
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

#root {
  position: relative;
  height: 100%;
}

.page-content {
  position: relative;
  top: 0;
  height: 300px;
  z-index: 100;
  background-color: white;
}

/* alert部分 */
.alter-text {
  position: absolute;
  left: 2%;
  top: 21%;
  color: green;
  font-size: 1.5rem;
  font-weight: 500;
}

.hr-line {
  position: absolute;
  top: 45%;
  width: 100%;
}

/* svg部分 */
svg.user-icon {
  position: absolute;
  left: 2.5%;
  top: 54%;
  width: 6%;
  height: 6% ;
}

.users-icon {
  position: absolute;
  left: 2.5%;
  top: 70%;
  width: 6%;
  height: 6% ;
}

svg.time-icon {
  position: absolute;
  left: 2%;
  top: 85%;
  width: 7.5%;
  height: 7.5%;
}

/* entryTitle部分 */
.entry-title {
  font-size: 1rem;
}

.title-user-name {
  position: absolute;
  left: 10%;
  top: 53%;
}

.title-assign-type {
  position: absolute;
  left: 10%;
  top: 69%;
}

.title-assign-time {
  position: absolute;
  left: 10%;
  top: 85%;
}

/* EntryText部分 */

.text-user-name {
  position: absolute;
  right: 3%;
  top: 52%;
  font-size: 1.5rem;
  font-weight: 500;
}

.text-assign-type {
  position: absolute;
  right: 3%;
  top: 68%;
  font-size: 1.5rem;
  font-weight: 500;
  color: rgb(236, 144, 53);
}

.text-assign-time {
  position: absolute;
  right: 3%;
  top: 85%;
  font-size: 1.1rem;
  font-weight: 550;
  color: rgb(88, 192, 165);
  overflow: hidden;
}

.moving-text {
  display: inline-block;
  font-size: 1.1rem;
  font-weight: 550;
  color: rgb(88, 192, 165);
  white-space: nowrap;
  animation: wordsLoop 5s linear infinite normal;
}

.text-assign-time2 {
  position: absolute;
  right: 3%;
  top: 85%;
  font-size: 1.1rem;
  font-weight: 600;
  color: rgb(88, 192, 165);
  overflow: hidden;
}

.moving-text2 {
  display: inline-block;
  font-size: 1.1rem;
  font-weight: 600;
  color: rgb(88, 192, 165);
  white-space: nowrap;
  animation: wordsLoop2 5s linear infinite normal;
}

/* 验证box */
.ant-form {
  position: absolute;
  top: 17%;
  width: 84%;
  left: 8%;
}

.ant-alert {
  position: absolute;
  top: 5%;
  width: 90%;
  left: 5%;
  height: 10%;
}


@keyframes wordsLoop {
  from {
    transform: translateX(0%);
  }
  to {
      transform: translateX(-105%);
  }
}

@keyframes wordsLoop2 {
  from {
    transform: translateX(105%);
  }
  to {
      transform: translateX(0%);
  }
}